import VisualComponent from "jx/core/comps/VisualComponent";
import Common from "jx/editor/editionitems/Common";
let clipInput = `
<div class="form-Group row">
    <div class="col-xs-4">
        <div class="input-Group">
            <input type="text" class="form-control" id="textSound" value="ID" readonly="true" >
        </div>
    </div>
    <div class="col-xs-2">
         <button class="btn btn-default" title="Voir"><span class="fa fa-eye"></span></button>
    </div>
    <div class="col-xs-3">
       <input id="text" type="text" class="form-control" value= "">
    </div>
    <div class="col-xs-3">
        <select id="sequenceContentX" class="form-control">
            <option value="VIDEO">Vidéo</option>
            <option value="SEQUENCE_IMAGES">Séquence d'Images</option>
            <option value="FLASH">Flash</option>
        </select>
    </div>
</div>
`;
class Clip extends VisualComponent {
    constructor(dataObject) {
        super(dataObject);
        this.controller = this.dataObject.controller;
        this.commonLib = new Common({ jx: this.jx });
        var item = $(clipInput);
        item.html(item[0].innerHTML.replace("ID", this.data.id).replace("DESCRIPTION", this.data.description || ""));
        this.item = item;
    }
}
export default Clip;
